import Vue from 'vue'
import Router from 'vue-router'
// import Index from '@/pages/index'
// import index from '@/pages/index/index'//
// import Onsaledetail from '@/pages/index/onsaledetail'
// import Collocation from '@/pages/index/collocation'

Vue.use(Router)

let router= new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      component: resolve=>require(['@/pages/index'],resolve),
      children:[
        {path:'',component:resolve=>require(['@/pages/index/index'],resolve),meta:{title:''}}
      ]
    },
    {
      path: '/onsaledetail',
      name: 'onsaledetail',
      component: resolve=>require(['@/pages/index/onsaledetail'],resolve),
      meta:{
          title:''
      }
    },
    {
      path: '/collocation',
      name: 'collocation',
      component: resolve=>require(['@/pages/index/collocation'],resolve),
      meta:{
          title:''
      }  
    }
    

  ]
})

////
// to  from next() 
router.beforeEach((to, from, next) => {
  //title
  changeTitle(to.meta.title);
  next();
});

//title
function changeTitle(title) {
  title = title ? title : '';
  window.document.title = title;
};


export default router



